import { Button } from "@mui/material";

export const MainButton = ({
  title,
  type,
  disabled,
  onClick
}: ButtonProps) => {
  return (
    <Button
      type={type}
      className="w-full"
      {...(onClick !== undefined ? { onClick } : undefined)}
      disabled={disabled}
      variant="contained"
      size="large"
      color="secondary"
      sx={{
        color: "white",
        padding: 2,
      }}
    >
      {title}
    </Button>
  );
};

interface ButtonProps {
    title: string;
    type?: 'button' | 'submit' | 'reset';
    disabled?: boolean;
    onClick?: () => void;
}
