import * as yup from 'yup';

export const mainSchema = yup.object({
  name: yup
    .string()
    .min(3, 'Имя должно быть не менее 3 символов')
    .required("Поле обязательно для заполнения"),
  email: yup
    .string()
    .email('Некорректный email')
    .matches(/\.[A-Z]{2,4}$/i, 'Некорректный email')
    .required("Поле обязательно для заполнения"),
  doctorType: yup
    .string()
    .required("Поле обязательно для заполнения"),
  // description: yup
  //   .string()
  //   .min(6, 'Опишите свой вопрос ')
  //   .required("Поле обязательно для заполнения"),
});

export const loginSchema = yup.object({
  email: yup
    .string()
    .email('Некорректный email')
    .matches(/\.[A-Z]{2,4}$/i, 'Некорректный email')
    .required("Поле обязательно для заполнения"),
  password: yup
    .string()
    .min(8, 'Должен быть не менее 8 символов')
    .matches(/[A-Z]/, 'Должен содержать хотя бы одну заглавную букву')
    .matches(/[a-z]/, 'Должен содержать хотя бы одну строчную букву')
    .matches(/[0-9]/, 'Должен содержать хотя бы одну цифру')
    .matches(/[!@#$%^&*]/, 'Должен содержать хотя бы один спецсимвол')
    .required('Поле обязательно для заполнения'),
});

