import { useEffect, useState } from 'react';
import { Spinner } from '../components/spinner/Spinner';
// import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Delete';
import { getCases } from '../api/getCases';
import { deleteCase } from '../api/deleteCase';
import { showToastMessage } from '../utils/showToastifyMessage';

enum PatientRequestStatus {
  OPENED = 'Открыта',
  PROCESSING = 'В работе',
  CLOSED = 'В архиве'
}

export interface Case {
  id: number;
  name: string;
  description: string;
  createdAt: string;
  email: string;
  doctorType: {
    id: number;
    name: string;
  };
  region: null | {
    id: number;
    name: string;
  };
  telegramContact: string | null;
  patientRequestStatus: keyof typeof PatientRequestStatus;
}

export const CasesPage = () => {
  const [cases, setCases] = useState<Case[]>([]);
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    const getData = async (): Promise<void> => {
      setLoading(true);

      const data = await getCases();
      setCases(data);
      console.log(data);

      setLoading(false);
    };
    getData().catch((e) => {
      console.error(e);
    });
  }, []);

  const deleteCaseHandler = async (id: number): Promise<void> => {
    const confirm = window.confirm('Вы уверены, что хотите удалить заявку?');
    if (!confirm) return;
    const resp = await deleteCase(id);
    if (resp.status === 200) {
      setCases(cases.filter((item) => item.id !== id));
      showToastMessage('Заявка удалена', 'green');
    } else {
      showToastMessage('Что-то пошло не так', 'red');
    }
  };

  if (loading) {
    return (
      <div className="container mx-auto mt-6 flex flex-col items-center gap-2 px-2 text-primary">
        <Spinner />
      </div>
    );
  }

  if (cases.length === 0) {
    return (
      <div className="container mx-auto flex flex-col items-center justify-center gap-2 px-2 text-primary">
        <p>Нет заявок</p>
      </div>
    );
  }

  return (
    <div className="container mx-auto flex flex-col items-center gap-2 px-2 text-lg text-primary">
      <h2 className='text-2xl'>Заявки</h2>
      <ul className="flex w-full max-w-[25rem] flex-col gap-2 sm:max-w-[45rem]">
        {cases.map((item) => {
          return (
            <li
              key={item.id}
              className="relative grid sm:grid-cols-2 sm:gap-2 rounded-md p-2 shadow-md"
            >
              <p>
                Имя: <span className="text-secondary">{item.name}</span>
              </p>
              <p>
                Специальность:{' '}
                <span className="text-secondary">{item.doctorType.name}</span>
              </p>
              <div>
                <p>
                  Почта: <span className="text-secondary">{item.email}</span>
                </p>
                {item.telegramContact !== null && (
                  <p>
                    Телеграм:{' '}
                    <span className="text-secondary">{item.telegramContact}</span>
                  </p>
                )}
              </div>
              {item.region !== null && (
                <p>
                  Регион: <span className="text-secondary">{item.region.name}</span>
                </p>
              )}
              {item.description !== '' && (
                <p className='sm:col-span-2'>
                  Описание:{' '}
                  <span className="text-secondary">
                    {item.description}
                    {/* Lorem ipsum dolor sit, amet consectetur adipisicing
                    elit. Officiis, quidem inventore. Architecto delectus, et hic
                    assumenda voluptas aliquam tempora, eos officiis corporis voluptatibus
                    iusto fugit, alias eaque distinctio enim necessitatibus. */}
                  </span>
                </p>
              )}
              <div className='flex flex-wrap gap-2 sm:col-span-2'>
                <p>
                  Создана:{' '}
                  <span className="text-secondary">
                    {new Date(item.createdAt).toLocaleDateString()}
                  </span>
                </p>
                <p>
                  Статус:{' '}
                  <span className="text-secondary">
                    {PatientRequestStatus[item.patientRequestStatus]}
                  </span>
                </p>
              </div>
              <div className="absolute right-0 top-0">
                <IconButton
                  aria-label="delete"
                  onClick={() => {
                    deleteCaseHandler(item.id).catch((e) => {
                      console.error(e);
                    });
                  }}
                  className="hover:text-primary"
                >
                  <DeleteIcon />
                </IconButton>
              </div>
            </li>
          );
        })}
      </ul>
    </div>
  );
};
