import { Formik, Form } from 'formik';
import type { initialValues } from './utils/initialValues';
import { Input } from '../../inputs/Input';
import { regions } from '../../inputs/utils/regions';
import { specialists } from '../../inputs/utils/specialists';
import { MainButton } from '../../button/MainButton';
import type { mainSchema } from '../validation/validationSchema';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { NavRoutes } from '../../../routesConfig';
import AutoComplete from '../../inputs/Autocomplete';
import type * as yup from 'yup';
import { Disclosure } from './Disclosure';
import { CheckBox } from '../../inputs/CheckBox';

export const MainForm = ({ onSubmit, mainSchema, initialValues }: MainFormProps) => {
  const [isTelegram, setIsTelegram] = useState<boolean>(false);
  const [isConfirm, setIsConfirm] = useState<boolean>(false);
  const navigate = useNavigate();

  const telegramClickHandler = () => {
    setIsTelegram(!isTelegram);
  };

  const confirmHandler = () => {
    setIsConfirm(true);
  };

  return (
    <Formik
      initialValues={initialValues}
      onSubmit={onSubmit}
      validationSchema={mainSchema}
      validateOnMount
    >
      {(formik) => {
        return (
          <Form className="flex w-full max-w-3xl flex-col gap-2">
            {[
              {
                name: 'name',
                label: 'Ваше имя',
                placeholder: 'Иванова Ивана Ивановна'
              },
              {
                name: 'email',
                label: 'Почта для связи',
                placeholder: 'mail@example.com'
              }
            ].map(({ name, label, placeholder }) => (
              <Input
                formik={formik}
                key={name}
                label={label}
                name={name}
                type="text"
                placeholder={placeholder}
                required={true}
              />
            ))}
            <CheckBox
              name="telegramToggle"
              label="Использую Telegram"
              onChange={telegramClickHandler}
              labekPlacement="start"
            />
            {isTelegram && (
              <Input
                label="Telegram"
                formik={formik}
                name="telegramContact"
                type="text"
                placeholder={'@ivanova123'}
              />
            )}
            <AutoComplete
              name={'region'}
              options={regions}
              formik={formik}
              label="Выберите регион"
            />
            <AutoComplete
              name={'doctorType'}
              options={specialists}
              formik={formik}
              label="Выберите специалиста"
              required={true}
            />
            <Input
              name="description"
              label="Суть проблемы"
              placeholder="Опишите клинический случай"
              formik={formik}
              type="text"
              textarea={true}
            />
            <CheckBox
              name="isSubscribedToNewsletter"
              label="Подписаться на рассылку"
              formik={formik}
              labekPlacement="start"
            />
            <MainButton
              title="Отправить заявку"
              type="button"
              onClick={confirmHandler}
              disabled={!formik.isValid}
            />
            <Disclosure />
            {isConfirm && (
              <div
                className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50 text-primary"
                onClick={(e) => {
                  if (e.target === e.currentTarget) {
                    setIsConfirm(false);
                  }
                }}
                onKeyDown={(e) => {
                  if (e.key === 'Escape') {
                    setIsConfirm(false);
                  }
                }}
                role="textbox"
                tabIndex={-1}
                aria-labelledby="new-address"
              >
                <div className="mx-2 flex max-w-[42rem] flex-col items-center justify-center gap-4 rounded bg-white p-4 sm:p-6">
                  <h3
                    className="text-center text-xl"
                    aria-label="new-address"
                  >
                    Проверьте корректность контактов
                  </h3>
                  <p className="w-full self-start rounded bg-slate-100 p-1">
                    Почта: <span className="text-orange">{formik.values.email}</span>
                  </p>
                  {formik.values.telegramContact !== '' ? (
                    <p className="w-full self-start rounded bg-slate-100 p-1">
                      Telegram:{' '}
                      <span className="text-orange">{formik.values.telegramContact}</span>
                    </p>
                  ) : null}
                  <MainButton
                    title="Подтвердить заявку"
                    type="submit"
                    onClick={() => {
                      formik.handleSubmit();
                      navigate(NavRoutes.donationPage);
                    }}
                  />
                </div>
              </div>
            )}
          </Form>
        );
      }}
    </Formik>
  );
};

interface MainFormProps {
  onSubmit: (values: Record<string, string | boolean>) => void;
  mainSchema: yup.ObjectSchema<yup.InferType<typeof mainSchema>>;
  initialValues: typeof initialValues;
}
