import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import type { FormikProps } from 'formik';
import type { InitialValuesMain } from '../form/MainForm/utils/initialValues';
import type { InitialValuesSignUp } from '../form/signUpForm/utils/initialValues';

interface AutoCompleteProps {
  name: string;
  options: string[];
  formik: FormikProps<InitialValuesMain> | FormikProps<InitialValuesSignUp>;
  label: string;
  required?: boolean;
}

export default function AutoComplete({
  name,
  options,
  formik,
  label,
  required
}: AutoCompleteProps) {
  const typedName = name as keyof typeof formik.values;
  return (
    <Autocomplete
      isOptionEqualToValue={(option, value) => {
        if (value === '') return true;
        return option === value;
      }}
      options={options}
      autoHighlight
      onChange={(e, value) => {
        formik.setFieldValue(name, value).catch((e) => {
          console.error(e);
        });
      }}
      value={formik.values[typedName]}
      onBlur={formik.handleBlur}
      sx={{
        '& .MuiOutlinedInput-root': {
          color: '#0c52aa',
          'fieldset': {
            borderColor: '#589af0'
          },
          '&:hover fieldset': {
            borderColor: '#0c52aa'
          },
          '&.Mui-focused fieldset': {
            borderColor: '#0c52aa'
          },
        },
        '& .MuiFormLabel-root': {
          color: '#589af0',
        },
      }}
      renderInput={(params) => (
        <TextField
          name={name}
          error={formik.touched[typedName] === true && Boolean(formik.errors[typedName])}
          helperText={
            formik.touched[typedName] === true && formik.errors[typedName] !== undefined
              ? formik.errors[typedName]
              : ''
          }
          {...params}
          label={label}
          required={required}
          inputProps={{
            ...params.inputProps
          }}
        />
      )}
    />
  );
}
