import { XMarkIcon } from '@heroicons/react/24/outline';
import type { ClickHandlerInterface } from '../../../../types';

export const BurgerCloseBtn = ({ clickHandler }: ClickHandlerInterface) => {
  return (
    <button
      type="button"
      className="-m-2.5 rounded-md p-2.5 text-primary hover:text-orange active:opacity-80 "
      onClick={() => {
        if (clickHandler === undefined) return;
        clickHandler(false);
      }}
    >
      <span className="sr-only">Close menu</span>
      <XMarkIcon
        className="h-6 w-6"
        aria-hidden="true"
      />
    </button>
  );
};
