// import { links } from '../../../../utils/links';
import { Link } from 'react-router-dom';
import type { ClickHandlerInterface } from '../../../types';
import { NavRoutes } from '../../../routesConfig';

export const NavGroup = ({ clickHandler }: ClickHandlerInterface) => {
  // const linksData = [links.main, links.catalog, links.about];
  const linksData = [
    { text: 'Заявка', path: NavRoutes.mainPagePath },
    { text: 'Все заявки', path: NavRoutes.casesPage },
    { text: 'Контакты', path: NavRoutes.donationPage },
    { text: 'Помочь', path: NavRoutes.donationPage },
    { text: 'Войти', path: NavRoutes.loginPage },
  ];
  return (
    <>
      {linksData.map((item) => (
        <li key={item.text} className='group flex items-center gap-2 hover:text-orange text-primary active:opacity-80  transition-colors'>
          <div className='h-2 w-2 bg-primary rounded-full group-hover:bg-orange transition-colors'></div>
          <Link
            {...(clickHandler !== undefined
              ? {
                  onClick: () => {
                    clickHandler(false);
                  },
                }
              : {})}
            to={item.path}
          >
            {item.text}
          </Link>
        </li>
      ))}
    </>
  );
};
