import { showToastMessage } from "../../../../utils/showToastifyMessage";

export const onSubmit = (values: Record<string, string | boolean>) => {
  console.log(values);
  fetch('http://89.223.68.190:8080/patient-requests', {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify(values),
  })
    .then(async (res) => {
      console.log(res);
      if (res.status === 201) {
        showToastMessage("Ваша заявка успешно отправлена", "green");
      } else {
        showToastMessage("Что-то пошло не так, попробуйте позже", "red");
      }
    }).catch((error) => {
      console.log(error);
      showToastMessage("Ошибка", "red");
    });
};
